ercase;\n  font-size: 16px;\n  user-select: none;\n}\n#loginBNuttonContainer {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin: auto;\n}\n.app-bar .app-bar__navigation {\n  display: flex;\n  justify-content: start;\n  position: absolute;\n  top: 50px;\n  left: -100%;\n  width: 100%;\n  transition: all 0.3s;\n  padding: 8px 15px;\n  background-color: white;\n  overflow: hidden;\n  z-index: 0;\n}\n \n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n\n#message {\n  padding: 10px;\n  text-align: center;\n}\n \n.app-bar .app-bar__navigation > ul > li {\n\n  list-style: none;\n  a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  padding: 14px 2px;\n  margin-bottom: 5px;\n  width: 100%;\n}\n}\n  /* Styling untuk hero section */\n#hero {\n  margin-top: -20px;\n  margin-bottom: 20px;\n  display: flex;\n  align-items: center;\n  justify-content: space-around;\n  padding: 50px 0;\n  background-color: #f2f2f2; /* Ubah warna latar sesuai kebutuhan */\n}\n\n#gambarhero {\n  display: grid;\n  grid-template-columns: repeat(2,1fr);\n  width: 100%;\n  justify-content:  center;\n  align-items: center;\n}\n\n#hero-text {\n  width: 60%;\n  text-align: left;\n  padding: 0 20px;\n  justify-content: center;\n}\n\n#hero-text h1 {\n  font-size: 2.5em;\n  margin-bottom: 15px;\n  color: #333; /* Ubah warna teks sesuai kebutuhan */\n}\n\n#hero-text p {\n  font-size: 1.2em;\n  color: #666; /* Ubah warna teks sesuai kebutuhan */\n}\n\n#hero-text button {\n  padding: 10px 20px;\n  background-color: $red; /* Warna latar belakang tombol */\n  color: #fff; /* Warna teks tombol */\n  border: none;\n  border-radius: 5px;\n  margin-top: 20px;\n  cursor: pointer;\n}\n.buttons { \n  list-style: none;\n  text-decoration: none;\n  display: block;\n  margin: auto;\n  padding: 10px 20px;\n  background-color: #db0000; /* Warna latar belakang tombol */\n  color: #fff; /* Warna teks tombol */\n  border: none;\n  border-radius: 5px;\n  margin-top: 20px;